<template>
  <loading :active="isLoading"></loading>
  <pagesBanner></pagesBanner>
  <section class="cart pages position-relative">
    <div class="container position-relative z-index-1 box_">
      <div class="bread-crumbs py-3 px-lg-5">
        <ol
          itemscope=""
          itemtype="http://schema.org/breadcrumbList"
          id="breadcrumbs"
        >
          <li
            itemprop="itemListElement"
            itemscope=""
            itemtype="http://schema.org/ListItem"
          >
            <router-link to="/" itemprop="item" title="劉記花生">
              <span itemprop="name">首頁</span>
            </router-link>
            <meta itemprop="position" content="1" />
          </li>
          <li
            itemprop="itemListElement"
            itemscope=""
            itemtype="http://schema.org/ListItem"
          >
            <span class="mx-2">/</span>
            <router-link to="/cart" itemprop="item" title="購物車清單">
              <span itemprop="name">購物車清單</span>
            </router-link>
            <meta itemprop="position" content="2" />
          </li>
        </ol>
      </div>
      <div class="main-box bg-white px-lg-5 px-4 py-lg-5 py-4">
        <div class="inner">
          <Form ref="form" action="" v-slot="{ errors }" @submit="sendOrder" class="px-xl-4">
            <div class="buy-list">
              <div class="top_title mb-3">商品清單</div>
              <div class="order-list">
                <div class="table-wrap">
                  <div class="table mb-4">
                    <div class="table-row">
                      <div class="td">產品照片</div>
                      <div class="td">產品名稱</div>
                      <div class="td">產品單價</div>
                      <div class="td w-qty">產品數量</div>
                      <div class="td">金額</div>
                      <div class="td">取消</div>
                    </div>
                    <div class="table-row" v-for="item in cart.carts" :key="item.id">
                      <div class="td title" data-title="產品照片">
                        <div>
                          <img :src="item.product.imageUrl" alt="" width="80" />
                        </div>
                      </div>
                      <div class="td title" data-title="產品名稱">
                        <div>
                          {{item.product.title}}
                          <div class="free_tr" v-if="item.coupon">
                            已套用優惠券
                          </div>
                        </div>
                      </div>
                      <div class="td title" data-title="產品單價">
                        <div>NT＄ {{ $filters.currency(item.product.price) }}</div>
                      </div>
                      <div class="td title" data-title="產品數量">
                        <div>
                          <div class="count">
                            <div class="row mx-0">
                              <button
                                class="col-auto value-button decrease_ btn btn-primary"
                                id=""
                                value="Decrease Value"
                                @click.prevent="item.qty -= 1;upData(item)"
                                :disabled="item.qty === 1 || loadingItem === item.id"
                              >
                                <svg-icon name="less"/>
                              </button>
                              <input
                                class="col qty px-1"
                                type="number"
                                min="1"
                                onkeyup="value=value.replace(/[^\d]+/g,'')"
                                v-model.number="item.qty"
                                @change="upData(item)"
                                :disabled="loadingItem === item.id"
                              />
                              <button
                                class="col-auto value-button increase_"
                                id=""
                                value="Increase Value"
                                @click.prevent="item.qty += 1;upData(item)"
                                :disabled="loadingItem === item.id"
                              >
                                <svg-icon name="add"/>
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div class="td title" data-title="金額">
                        <div>
                          <div :class="{'fst-italic text-decoration-line-through': item.coupon}">NT＄ {{ $filters.currency(item.total) }}</div>
                          <div class="free_tr" v-if="item.coupon">
                            NT＄ {{ $filters.currency(item.final_total) }}
                          </div>
                        </div>
                      </div>
                      <div class="td title" data-title="取消">
                        <div>
                          <a href="" @click.prevent="deleteData(item)">刪除</a>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="row justify-content-between">
                  <div class="col-lg-6 mb-4">
                    <div class="row mx-0">
                      <label
                        for="message"
                        class="col-md-auto mb-2 d-flex justify-content-md-end align-items-center px-0 pe-md-3"
                        >留言</label
                      >
                      <textarea
                        name=""
                        id=""
                        cols="30"
                        rows="5"
                        class="col-md mb-2"
                        v-model="form.message"
                      ></textarea>
                    </div>
                  </div>
                  <div class="col-lg-5 mb-4">
                      <div class="mb-3 row flex-nowrap align-items-center">
                        <span class="col text-end">產品總金額</span>
                        <span id="ori_total" class="col-4 text-end">{{ $filters.currency(cart.total) }}</span>
                      </div>
                      <div class="mb-3 row flex-nowrap align-items-center">
                        <span class="col text-end">(配送地區:限臺灣本島)<br>運費總金額</span>
                        <span class="col-4 text-end">0</span>
                      </div>
                      <hr>
                      <div class="mb-3 row flex-nowrap align-items-center">
                        <span class="col text-end">滿額免運費(滿2000免運)</span>
                        <span id="off_fare" class="col-4 text-end free_tr">- 0</span>
                      </div>
                      <div class="mb-3 row flex-nowrap gold align-items-center" v-if="cart.total !== cart.final_total">
                        <span class="col text-end">折抵金額</span>
                        <span class="col-4 free_tr text-end">- {{ cart.total - cart.final_total }}</span>
                      </div>
                      <div class="mb-3 row flex-nowrap gold align-items-center">
                        <span class="col text-end">優惠券代碼</span>
                        <span class="col-4 d-flex align-items-center justify-content-end">
                          <input type="text" name="coupon" id="coupon" style="width: 80px;" v-model="coupon">
                          <button class="bg-primary text-white coupon-btn" @click.prevent="addCoupon()">使用</button>
                        </span>
                      </div>
                      <hr>
                      <div class="mb-3 row flex-nowrap total align-items-center">
                        <span class="col text-end">總付款金額</span>
                        <span id="total" class="col-4 text-end total_txt">{{ $filters.currency(cart.final_total) }}</span>
                      </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="pay">
              <div class="top_title">請選擇 付款方式</div>
              <hr />
              <div class="row mb-5 pt-lg-3">
                <div class="radio-group mb-3 col-12">
                  <input
                    type="radio"
                    class="radio-input"
                    id="pay1"
                    name="pay"
                  />
                  <label for="pay1" class="radio-label">
                    <span class="radio-button"></span>
                    匯款 / 轉帳分行 / 玉山銀行羅東分行  戶名 / 林佳儒  帳號
                    / 0451968170771
                  </label>
                </div>
                <div class="radio-group col-12">
                  <input
                    type="radio"
                    class="radio-input"
                    id="pay2"
                    name="pay"
                  />
                  <label for="pay2" class="radio-label">
                    <span class="radio-button"></span>
                    貨到付款<br />
                    (10000元以上請先匯款，因應黑貓宅急便運費以及代收手續費持續調漲，故即日起貨到付款需酌收部份代收手續費，請見諒！
                    )
                  </label>
                </div>
              </div>
            </div>
            <div class="member">
              <div class="top_title">請填寫 訂單資料</div>
              <hr />
              <div class="row justify-content-center">
                <div class="col-xl-6 col-lg-10">
                  <div class="mb-4">
                    <div class="row mx-0">
                      <div class="col-md-4 d-none d-xl-block"></div>
                      <h3 class="text-center col-xl mb-md-4">訂購人</h3>
                    </div>
                    <div class="row mb-3 mx-0">
                      <label
                        for="name"
                        class="col-md-4 mb-2 d-flex justify-content-md-end align-items-center px-0 pe-md-3"
                        >姓名</label
                      >
                      <div class="col-md mb-2 px-0">
                        <input type="text" id="name" class="form-control w-100" />
                      </div>
                    </div>
                    <div class="row mb-3 mx-0">
                      <label
                        for="email"
                        class="col-md-4 mb-2 d-flex justify-content-md-end align-items-center px-0 pe-md-3"
                        >電子信箱*</label
                      >
                      <div class="col-md mb-2 px-0">
                        <Field id="email" name="email" type="email" class="form-control"
                          :class="{ 'is-invalid': errors['email'] }"
                          placeholder="請輸入 Email" rules="email|required"
                          v-model="form.user.email"></Field>
                        <ErrorMessage name="email" class="invalid-feedback"></ErrorMessage>
                      </div>
                    </div>
                    <div class="row mb-3 mx-0">
                      <label
                        for="tel"
                        class="col-md-4 mb-2 d-flex justify-content-md-end align-items-center px-0 pe-md-3"
                        >訂購人電話</label
                      >
                      <div class="col-md mb-2 px-0">
                        <input type="tel" id="tel" class="form-control w-100" />
                      </div>
                    </div>
                    <div class="row mb-3 mx-0">
                      <label
                        for="acct"
                        class="col-md-4 mb-2 d-flex justify-content-md-end align-items-center px-0 pe-md-3"
                        >轉帳帳號末五碼</label
                      >
                      <div class="col-md mb-2 px-0">
                        <input type="text" id="acct" class="form-control w-100" />
                      </div>
                    </div>
                    <div class="row mb-3 mx-0">
                      <label
                        for="company2"
                        class="col-md-4 mb-2 d-flex justify-content-md-end align-items-center px-0 pe-md-3"
                        >傳統發票打統編(選填)</label
                      >
                      <div class="col-md mb-2 px-0">
                        <input
                          type="text"
                          id="company2"
                          placeholder="傳統直式發票打統編請填寫"
                          class="form-control w-100" />
                      </div>
                    </div>
                    <div class="row mb-3 mx-0">
                      <label
                        for="company"
                        class="col-md-4 mb-2 d-flex justify-content-md-end align-items-center px-0 pe-md-3"
                        >公司抬頭(選填)</label
                      >
                      <div class="col-md mb-2 px-0">
                        <input
                          type="text"
                          id="company"
                          placeholder="如需開立三聯發票請填寫"
                          class="form-control w-100" />
                      </div>
                    </div>
                    <div class="row mb-3 mx-0">
                      <label
                        for="tax_id"
                        class="col-md-4 mb-2 d-flex justify-content-md-end align-items-center px-0 pe-md-3"
                        >統一編號(選填)</label
                      >
                      <div class="col-md mb-2 px-0">
                        <input
                          type="text"
                          id="tax_id"
                          placeholder="如需開立三聯發票請填寫"
                          class="form-control w-100" />
                      </div>
                    </div>
                  </div>
                </div>
                <div class="col-xl-6 col-lg-10">
                  <div class="mb-4">
                    <div class="row mx-0">
                      <div class="col-md-4 d-none d-xl-block"></div>
                      <h3 class="text-center col-xl mb-md-4">收件人</h3>
                    </div>
                    <div class="row mb-3 mx-0">
                      <label
                        for="r_name"
                        class="col-md-4 mb-2 d-flex justify-content-md-end align-items-center px-0 pe-md-3"
                        >收件人*</label
                      >
                      <div class="col-md mb-2 px-0">
                        <Field id="name" name="r_name" label="姓名" type="text" class="form-control"
                          :class="{ 'is-invalid': errors.r_name }"
                          placeholder="請輸入姓名" rules="required"
                          v-model="form.user.name"></Field>
                        <ErrorMessage name="r_name" class="invalid-feedback"></ErrorMessage>
                      </div>
                    </div>
                    <div class="row mb-3 mx-0">
                      <label
                        for="r_phone"
                        class="col-md-4 mb-2 d-flex justify-content-md-end align-items-center px-0 pe-md-3"
                        >收件人手機*</label
                      >
                      <div class="col-md mb-2 px-0">
                        <Field id="tel" name="r_phone" label="電話" type="tel" class="form-control"
                          :class="{ 'is-invalid': errors.r_phone }"
                          placeholder="請輸入電話" rules="required"
                          v-model="form.user.tel"></Field>
                        <ErrorMessage name="r_phone" class="invalid-feedback"></ErrorMessage>
                      </div>
                    </div>
                    <div class="row mb-3 mx-0">
                      <label
                        for="r_tel"
                        class="col-md-4 mb-2 d-flex justify-content-md-end align-items-center px-0 pe-md-3"
                        >市話</label
                      >
                      <div class="col-md mb-2 px-0">
                        <input type="tel" id="r_tel" class="form-control w-100" />
                      </div>
                    </div>
                    <div class="row mb-3 mx-0">
                      <label
                        for="r_county"
                        class="col-md-4 mb-2 d-flex justify-content-md-end align-items-center px-0 pe-md-3"
                        >城市/縣</label
                      >
                      <div class="col-md mb-2 px-0">
                        <input type="text" id="r_county" class="form-control w-100" />
                      </div>
                    </div>
                    <div class="row mb-3 mx-0">
                      <label
                        for="r_area"
                        class="col-md-4 mb-2 d-flex justify-content-md-end align-items-center px-0 pe-md-3"
                        >地區</label
                      >
                      <div class="col-md mb-2 px-0">
                        <input type="text" id="r_area" class="form-control w-100" />
                      </div>
                    </div>
                    <div class="row mb-3 mx-0">
                      <label
                        for="r_address"
                        class="col-md-4 mb-2 d-flex justify-content-md-end align-items-center px-0 pe-md-3"
                        >街道地址*</label
                      >
                      <div class="col-md mb-2 px-0">
                        <Field id="address" name="r_address" label="地址" type="text" class="form-control"
                          :class="{ 'is-invalid': errors.r_address }"
                          placeholder="請輸入地址" rules="required"
                          v-model="form.user.address"></Field>
                        <ErrorMessage name="r_address" class="invalid-feedback"></ErrorMessage>
                      </div>
                    </div>
                    <div class="row mb-3 mx-0">
                      <label
                        for="datepicker"
                        class="col-md-4 mb-2 d-flex justify-content-md-end align-items-center px-0 pe-md-3"
                        >希望收貨日期</label
                      >
                      <div
                        class="col-md px-0 d-flex flex-wrap align-items-center"
                      >
                        <div class="col-md-4 col-12 mb-2">
                          <VueDatePicker v-model="date" :min-date="new Date(new Date().getTime()+(3*24*60*60*1000))" auto-apply :enable-time-picker="false"></VueDatePicker>
                        </div>
                        <label
                          for="datepicker"
                          class="col-md-4 col-12 mb-2 d-flex justify-content-md-center align-items-center px-0 px-md-2"
                          >收貨時間</label
                        >
                        <select name="" id="" class="mb-2 col form-control">
                          <option value="">上午</option>
                          <option value="">下午</option>
                        </select>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <hr />
            <div class="row justify-content-lg-end justify-content-center mx-0">
              <button class="btn btn-primary px-5 col-auto">送出訂單</button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
import pagesBanner from '../components/pagesBanner.vue'
import emitter from '../methods/emitter'
import VueDatePicker from '@vuepic/vue-datepicker'
import '@vuepic/vue-datepicker/dist/main.css'
export default {
  components: {
    pagesBanner,
    VueDatePicker
  },
  data () {
    return {
      isLoading: false,
      loadingItem: '',
      cart: [],
      date: null,
      coupon: '',
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: ''
        },
        message: ''
      }
    }
  },
  watch: {
    cart () {
      if (this.cart.carts.length === 0) {
        this.$router.push('/')
      }
    }
  },
  methods: {
    simpleScrollTrigger () {
      this.gsap.from('.box_', {
        y: 50,
        opacity: 0,
        duration: 1,
        delay: 0.5,
        scrollTrigger: {
          trigger: '.box_',
          start: 'top 90%'
        }
      })
    },
    getCart () {
      this.isLoading = true
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`
      this.$http.get(api).then((res) => {
        this.isLoading = false
        if (res.data.success) {
          this.cart = res.data.data
          if (res.data.data.total !== res.data.data.final_total) {
            this.coupon = res.data.data.carts[0].coupon.code
          }
        }
      })
    },
    upData (item) {
      if (item.qty === 0) {
        item.qty = 1
      }
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart/${item.id}`
      const cart = {
        product_id: item.id,
        qty: item.qty
      }
      this.loadingItem = item.id
      this.$http.put(api, { data: cart }).then((res) => {
        if (res.data.success) {
          this.getCart()
          emitter.emit('getData')
          this.loadingItem = ''
        }
      })
    },
    deleteData (item) {
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart/${item.id}`
      this.$http.delete(api).then((res) => {
        if (res.data.success) {
          this.getCart()
          emitter.emit('getData')
        }
      })
    },
    addCoupon () {
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/coupon`
      this.$http.post(api, { data: { code: this.coupon } }).then((res) => {
        this.getCart()
        console.log(res.data.message)
        this.$httpMessage(res, '使用優惠券')
      })
    },
    sendOrder () {
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/order`
      const order = this.form
      this.$http.post(api, { data: order }).then((res) => {
        if (res.data.success) {
          console.log(res.data.message)
          this.$router.push(`/completed/${res.data.orderId}`)
        }
      })
    }
  },
  mounted () {
    this.getCart()
    this.simpleScrollTrigger()
  }
}
</script>
